.works {
    min-height: 100vh;
    background-color: #1f1f20;
    display: flex;
    justify-content: center;
    align-items: flex-start;
}

.container {
    min-width: 80%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    padding-top: 50px;
}

.works__items {
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    flex-wrap: wrap;
}

@media screen and (max-width: 1050px ) {
    .works__items  {
        justify-content: center;
        align-items: center;
        margin: 40px;
    }
}

@media screen and (max-width: 600px ) {
    .container {
        padding-top: 10px;
    }

}