.main {
    min-height: 100vh;
    background-color: #1f1f20;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 100px 0;
}

.container {
    width: 80%;
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-wrap: wrap;

}

.main__greeting {
    display: flex;
    flex-direction: column;
    font-weight: 600;
    flex-wrap: wrap;
}

.greeting__item {
    font-size: 16px;
}

.greeting__name,
.greeting__surname {
    font-weight: 700;
    font-stretch: normal;
    font-size: 50px;
    line-height: 1.2;
    font-family: Montserrat, sans-serif;
    letter-spacing: 5px;
    position: relative !important;
    z-index: 0;
}

.greeting__surname:before {
    content: '';
    position: absolute;
    width: 100%;
    height: 20px;
    background-color: darkcyan;
    bottom: 0px;
    z-index: -1;

}

.greeting__title {
    font-size: 28px;
}


.main__photo {
    margin-top: 50px;
    position: relative;
    z-index: 1;
}


.main__image {
    display: block;
    height: 400px;
    width: 300px;
    background-image: url("../../image/1.png");
    background-repeat: space;
}


.main__image:before {
    content: '';
    position: absolute;
    height: 430px;
    width: 300px;
    border: 3px solid darkcyan;
    left: 15px;
    top: -15px;
    z-index: -1;
}

@media screen and (max-width: 680px ) {
    .main__greeting {
        text-align: center;
    }
}


@media screen and (max-width: 500px ) {
    .greeting__surname:before {

        width: 100%;
        bottom: 0px;

    }
}

